import type { NextRequest } from "next/server";

type Ctx = { params: { fn: string } };

// Run this on Vercel Edge
export const runtime = "edge";
export const preferredRegion = ["bom1", "sin1"];

function withCors(init: ResponseInit = {}): ResponseInit {
  const headers = new Headers(init.headers);
  headers.set("Access-Control-Allow-Origin", "*");
  headers.set("Access-Control-Allow-Methods", "GET,POST,OPTIONS");
  headers.set("Access-Control-Allow-Headers", "Content-Type, Authorization");
  headers.set("Content-Type", "application/json; charset=utf-8");
  return { ...init, headers };
}

function placeholder(fn: string) {
  return {
    ok: true,
    message: "Edge handler online. Specific handlers come in Step 2.",
    requested: fn,
    time: new Date().toISOString(),
    runtime: "edge",
  };
}

export async function GET(_req: NextRequest, ctx: Ctx) {
  return new Response(JSON.stringify(placeholder(ctx.params.fn)), withCors({ status: 200 }));
}

export async function POST(_req: NextRequest, ctx: Ctx) {
  return new Response(JSON.stringify(placeholder(ctx.params.fn)), withCors({ status: 200 }));
}

export async function OPTIONS() {
  return new Response(null, withCors({ status: 204 }));
}
